<template>
  <div class="order-success">
    <v-dialog class="dialog" v-model="dialog" width="50%" persistent>
      <v-card class="text-center py-7">
        <div class="text-center">
          <v-icon
            style="
              background: white;
              width: 100px;
              height: 100px;
              color: green;
              border: 1px solid green;
              font-size: 76px;
              border-radius: 50%;
            "
            >mdi-check</v-icon
          >
        </div>
        <v-card-title
          style="font-size: 30px; font-weight: bold; color: rgb(93, 93, 93)"
          >Order placed Successfuly!</v-card-title
        >
        <v-card-text
          style="font-size: 17px; color: rgb(119, 117, 117)"
          class="px-20"
          >Lorem ipsum dolor sit amet consectetur, adipisicing elit. Iste error
          quo cum explicabo iure ex nisi, nesciunt laboriosam saepe sit at atque
          magni et in, nulla nam exercitationem culpa natus.</v-card-text
        >
        <v-card-actions class="mt-5 justify-center">
          <v-btn
            variant="elevated"
            color="blue"
            elevation="0"
            @click="resetData"
            >Got it!</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { cartStore } from "@/stores/cart";
import { mapActions } from "pinia";

export default {
  data: () => ({
    dialog: false,
  }),
  props: {
    popup: {
      type: Boolean,
    },
  },
  watch: {
    dialog(newVal) {
      if (!newVal) {
        setTimeout(() => {
          this.$emit("close_popup");
        }, 200);
      }
    },
  },
  methods: {
    ...mapActions(cartStore, ["resetItems"]),
    resetData() {
      this.resetItems();
      this.dialog = false;
      this.$router.push({ name: "home" });
    },
  },
  mounted() {
    this.dialog = this.popup;
  },
};
</script>

<style lang="scss">
/* Media Queries */
@media (max-width: 990px) {
  .v-overlay__content {
    width: 100% !important;
  }
}
</style>
